.header_flex{
    display: flex;
    justify-content: space-between;
    align-items: center;
 /*    background-color: bisque; */
    padding: 20px;
    position: fixed;
    
    width: 100%;
    top: 0;
    box-shadow: 0 0 0.7142857142857143rem #08850c;
    z-index: 1000;
    
}




.nav ul{
    display: flex;
    font-size: 20px;
    transition: all 0.4s;


}

.nav ul li{
  
    list-style: none;
}
.nav ul li a{
    margin: 20px;
    text-decoration: none;
}
.social_icons{
    font-size: 2em;
    
    
}

.logo{
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.logo img{
    width: 60px;
}


.videoTag{
    position: fixed;
    width: 100%;
    height: 100vh;
    /* height: 100%; */
    object-fit: cover;
    top: 0;
    z-index: -1;
    
    
    
}

@media(max-width:768px){
    .nav {
        position: absolute;
        top:90px;
        left:-100%;
        width: 200px;
        transition: all 0.4s;
        height: 100vh;
        background-color: rgba(0,0,0,0.5);
        list-style: none;
        
      
      }

   


    .mostrar {
        left: 0;
    
      }
      .opacar {
        background-color: rgba(0,0,0,0.5);
      }

 
    
      nav ul{
          flex-direction: column;
          display: flex;
          padding: 0;
      }
   .nav ul li {
        
        margin-top: 20px;
      }
      #icon-menu{
          cursor: pointer;
          font-size: 2em;
          
          
      }
      .logo h2{
          display: none;
      }
     
}
@media(min-width:768px){
  #icon-menu{
      display: none;
  }
}

/* -----------Animaci√≥n Menu boton--------------- */

.menu-bar{
    width: 30px;
    cursor: pointer;
    
}

.uno, .dos, .tres{
    width: 100%;
    background: white;
    height: 3px;
    transition: all 0.5s;
}

.dos{
    margin: 5px 0;
}


.btn_x .dos{
    opacity:0;

}

.btn_x .uno{
    transform: rotate(45deg) translate(5px, 5px);
  
}
.btn_x .tres{
     transform: rotate(-45deg) translate(6px, -7px);
}



